'use client';

import { motion } from 'framer-motion';
import { useState } from 'react';
import Image from 'next/image';
import Container from '../ui/Container';
import Section from '../ui/Section';

const practiceAreas = [
  {
    title: 'Luxury Retail & E-Commerce',
    description: 'We construct digital storefronts that sell luxury the way it\'s bought, with trust, taste, and quiet exclusivity.',
    iconSrc: '/images/practice/luxury.png',
  },
  {
    title: 'UX/UI Experience Design',
    description: 'Form follows feeling, powered by function.\nSystems are clean, usable, and intuitive.',
    iconSrc: '/images/practice/uxui.png',
  },
  {
    title: 'Technical Excellence',
    description: 'Headless. Secure. Scalable.\nWe build for longevity.',
    iconSrc: '/images/practice/tech.png',
  },
];

export default function PracticeAreas() {
  const [hovered, setHovered] = useState<number | null>(null);
  return (
    <Section className="bg-bg-primary py-24">
      <Container>
        <div className="grid md:grid-cols-3 gap-8 items-stretch">
          {practiceAreas.map((area, index) => (
            <motion.div
              key={area.title}
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: index * 0.2 }}
              viewport={{ once: true }}
              className={`group border border-text-base/10 p-8 rounded-lg transition-colors h-full min-h-[280px] flex flex-col items-center text-center hover:bg-cta-brass hover:border-cta-brass ${hovered === index ? 'text-black' : 'text-text-base'}`}
              onMouseEnter={() => setHovered(index)}
              onMouseLeave={() => setHovered(null)}
            >
              {/* Icon */}
              <div className="w-24 h-24 p-2 rounded-lg flex items-center justify-center mb-6 mx-auto border-0 ring-0 outline-none shadow-none">
                <Image
                  src={area.iconSrc}
                  alt={area.title}
                  width={88}
                  height={88}
                  className="object-contain border-0 ring-0 outline-none shadow-none bg-transparent w-full h-full max-w-full max-h-full"
                />
              </div>
              
              {/* Title */}
              <h3
                className={`font-serif text-xl mb-4 transition-colors ${hovered === index ? 'text-black' : 'text-text-base'}`}
                style={{ color: hovered === index ? '#000000' : undefined }}
              >
                {area.title}
              </h3>
              
              {/* Description */}
              <p
                className={`font-sans text-sm leading-relaxed whitespace-pre-line transition-colors ${hovered === index ? 'text-black/90' : 'text-text-base/70'}`}
                style={{ color: hovered === index ? '#000000E6' : undefined }}
              >
                {area.description}
              </p>
            </motion.div>
          ))}
        </div>
      </Container>
    </Section>
  );
}
